import Section from "../../common/Section";
import Container from "../../common/Container";
import Header from "../../common/Header";

const AuthorPage = () => (
  <Container>
    <Header title={"O autorze"} />
    <Section
      title={"Krystian ybura"}
      body={
        <article>
          Znasz ju moje imi, obecnie mam 21 lat i{" "}
          <strong>
            posiadam niesamowit pasj w stosunku do urzdze elektronicznych
          </strong>
          . Interesuje mnie ich budowa, czyli z czego dokadnie si skadaj,
          jak s zaprogramowane oraz wiedza na temat ich moliwoci, czy te
          jakie moemy wprowadzi usprawniania, aby dziaay one wydajniej. Nie
          zdziwi ci pewnie fakt, e z zawodu jestem informatykiem. Po
          ukoczeniu szkoy redniej postanowiem kontynuowa rozwijanie si w
          tym kierunku zawodowym a jako, e jest to dziedzina niezwykle obszerna
          i mona j zgebia w zasadzie bez koca, to finalnie zdecydowaem si
          na rozpoczcie nauki programowania.
        </article>
      }
    />
  </Container>
);

export default AuthorPage;
